.App {
    background: #92C1F0;
}

.home-grid {
    background: #92C1F0;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.banner-text h1 {
    font-size: 70px;
    color: black;
}

.banner-text p {
    font-size: 25px;
    color: black;
}

.banner-text br {
    content: "";
    margin: 2em;
    display: block;
    font-size: 15%;
}

.banner-text hr {
    border-top: 1px solid black;
    width: 330px;
    margin: auto;
}

.social-links {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 25px;
}

.social-links img {
    font-size: 5em;
    padding: 8px;
}

.resume-icon {
    height: 75px;
}

.projects-icon {
    height: 80px;
}

.github-icon {
    height: 80px;
}

.linkedin-icon {
    height: 65px;
}

.email-icon {
    height: 65px;
}